import React from "react";
import { Switch, Route } from "react-router-dom";
// import Home from "./components/Home/Home";
import Layout from "./components/Hoc/Layout";
import Auth from "./components/Hoc/Auth";
import RegisterLogin from "./components/Register_Login/RegisterLogin";
import Register from "./components/Register_Login/Register";
import Userdashboard from "./components/User/Userdashboard";

const App = () => {
  return (
    <Layout>
      <Switch>
        <Route
          path="/e-commerce-fullstack-app/user/dashboard"
          exact
          component={Auth(Userdashboard, true)}
        />
        <Route
          path="/e-commerce-fullstack-app/register"
          exact
          component={Auth(Register, false)}
        />
        <Route
          path="/e-commerce-fullstack-app/"
          exact
          component={Auth(RegisterLogin, false)}
        />
        {/* <Route
          path="/e-commerce-fullstack-app/"
          exact
          component={Auth(Home, null)}
        /> */}
      </Switch>
    </Layout>
  );
};

export default App;
